body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



/* App.css */
.container {
  text-align: center;
  margin-top: 50px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.task-list {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.task-item {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  margin: 10px 10px 10px 10px;
  cursor: pointer;
}

.persons {
  display: flex;
  justify-content: center;
}

.person {
  margin: 0 20px;
}

.person h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.person p {
  font-size: 16px;
}





















.card-grid {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.card {
  width: 200px;
  height: 200px;
  background-color: #007bff;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  cursor: pointer;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  margin: auto;
}

.card.flipped .card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card .card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.card .card-front,
.card .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card .card-front {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
}

.card .card-back {
  background-color: #007bff;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
}

.input-section .form-control {
  width: 100%;
}

.input-section .btn {
  width: 100%;
}

.row {
  margin-right: -15px;
  margin-left: -15px;
}

.col-md-4 {
  padding-right: 15px;
  padding-left: 15px;
}

.mb-3 {
  margin-bottom: 1rem;
}

.mt-5 {
  margin-top: 3rem;
}

.mb-4 {
  margin-bottom: 2rem;
}
